
 import React,{useState} from 'react'; 
 import {Card,Breadcrumb,CardBody,CardText, BreadcrumbItem,Button}from 'reactstrap';
 import {Link} from 'react-router-dom';
 

 /* hm RenderSalary render ra ni dung tng nhn vin */
 function RenderSalary({staff,salary}){
 
   return(
    <div>
      <Card key={staff.id} >                    
        <CardBody>
          <CardText>
              <Link to ={`/staff/${staff.id}`}><b>{staff.name}</b> </Link>
          </CardText>
          <CardText>M nhn vin: {staff.id}</CardText>
          <CardText>H s lng: {staff.salaryScale}</CardText>
          <CardText>S ngy lm thm: {staff.overTime}</CardText>
          <CardText className='row col-12'>Lng: {salary} VND </CardText>
        </CardBody>                    
      </Card>
    </div>
   );
 }

/* Hm render ra ton b trang bng lng nhn vin */
 function Salary(props){
      const [stafflist, setStaffList] = useState(props.staff);
/* Hm tnh lng nhn vin */
     function SalaryCalculation(salaryScale,overTime){         
      return salaryScale*10*300000 + overTime*200000;
     } 
/* hm sp xp lng nhn vin */
      function sortSalary(sort){
      let sortSalaryList=[...stafflist]
      let x=0;
      let y=0;
      if (sort==="luong thap"){
         sortSalaryList.sort(function(a,b){
          x=SalaryCalculation(a.salaryScale,a.overTime);
          y=SalaryCalculation(b.salaryScale,b.overTime);
          return x-y;
         })
      }
      if (sort==="luong cao"){
        sortSalaryList.sort(function(a,b){
         x=SalaryCalculation(a.salaryScale,a.overTime);
         y=SalaryCalculation(b.salaryScale,b.overTime);
         return y-x;
        })
     }
     setStaffList(sortSalaryList);
     } 
/* hm sp xp thi gian overTime ca nhn vin */
     function sortOverTime(sort){
      let sortOverTime=[...stafflist]
      let x=0;
      let y=0;
      if (sort==="tang ca thap"){
         sortOverTime.sort(function(a,b){
          x=a.overTime;
          y=b.overTime;
          return x-y;
         })
      }
      if (sort==="tang ca cao"){
        sortOverTime.sort(function(a,b){
          x=a.overTime;
          y=b.overTime;
         return y-x;
        })
     }
     setStaffList(sortOverTime);
     }      


      console.log(stafflist);
         const salary=stafflist.map((staff)=>{
             return(
                 <div className="col-12 col-md-6 col-lg-4 mt-3">
                     <RenderSalary staff={staff} salary={SalaryCalculation(staff.salaryScale,staff.overTime)}/>
                 </div>                
             );
         });
       
         return(
             <div class="container">                  
                 <div className='col-12'>
                     <Breadcrumb>
                         <BreadcrumbItem><Link to="/staff">Nhn vin</Link></BreadcrumbItem>
                         <BreadcrumbItem><Link to="/department">phng ban</Link></BreadcrumbItem>
                         <BreadcrumbItem active> Bng Lng</BreadcrumbItem>
                     </Breadcrumb>                     
                     <hr/>                    
                 </div> 
                 
                <div className='row'>
                  <div className='col-12 col-lg-6'>
                    <h5>Sp xp theo mc lng</h5>
                    <Button className='m-2' onClick={()=>sortSalary("luong thap")}><i class="fa fa-arrow-up"></i> Lng Thp</Button>
                    <Button className='m-2' onClick={()=>sortSalary("luong cao")}><i class="fa fa-arrow-down"></i> Lng cao</Button>
                  </div>
                  <div className='col-12 col-lg-6'>
                    <h5>Sp xp theo thi gian tng ca</h5>
                    <Button className='m-2' onClick={()=>sortOverTime("tang ca thap")}><i class="fa fa-arrow-up"></i> Thi gian tng ca Thp</Button>
                    <Button className='m-2' onClick={()=>sortOverTime("tang ca cao")}><i class="fa fa-arrow-down"></i> Thi gian tng ca cao</Button>
                  </div>                  
                 
                </div>      
                 <div class="row"> 
                     {salary}
                 </div> 
                 <hr/>                     
             </div>
         );
     }
 
 
 

export default Salary;